export class Command {

  public commandName: string;
  public execute = (obj?: any) => Command.SendCommand({ command: this.commandName, ...obj });


  constructor(commandName: string) {
    this.commandName = commandName;
  }

  public static GenerateIdentity = new Command('GenerateIdentity');
  public static FetchAllIdentityName = new Command('FetchAllIdentityName');
  public static SendIdentity = new Command('SendIdentity');
  public static DeleteIdentity = new Command('DeleteIdentity');

  public static async SendCommand(request: any): Promise<any> {
    return new Promise((resolve, reject) => {
      console.log(`${request.command} Request`, request);

      chrome.runtime.sendMessage(request, function (response) {
        console.log(`${request.command} Response`, response);
        resolve(response);
      });
    });
  }
}
