import React, { Fragment, useState } from "react";
import { makeStyles } from '@material-ui/core/styles';
import TextField from '@material-ui/core/TextField';
import ColorButton from '../core/ColorButton';
import { Box } from "@material-ui/core";

function AddIdentity(props: any) {

  const useStyles = makeStyles((theme) => ({
    root: {
      flexGrow: 1,
    },
    h3: {
      marginTop: theme.spacing(4),
    },
    nameInput: {
      flex: 1,
      marginTop: theme.spacing(1),
    },
    form: {
      display: 'flex',
      flexDirection: 'column',
      alignItems: '',
      width: 400,
    },
    action: {
      display: 'flex',
      flexDirection: 'row',
      marginTop: theme.spacing(3),
      justifyContent: 'space-between',
      '& .caption': {
        color: '#A9A9A9',
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'center',
        '& span': {
          fontSize: '12px',
          lineHeight: '16px',
          textAlign: 'left'
        },
        '& a':{
          color: '#0A6CFF'
        }
      }
    },
    bottom: {
      display: 'flex',
      flexDirection: 'row',
      marginTop: theme.spacing(6),
      justifyContent: 'center',
      '& .caption': {
        color: '#A9A9A9',
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'center',
        '& span': {
          fontSize: '12px',
          lineHeight: '16px',
          textAlign: 'center'
        }
      }
    },
  }));

  const classes = useStyles();

  const [name, setName] = useState<string>('');

  const _handleTextFieldChange = (e: React.ChangeEvent<HTMLTextAreaElement | HTMLInputElement>) => {
    setName(e.target.value.toLowerCase());
  }

  const regexDomain = /^((xn--)?([a-z0-9-]{1,61}|[a-z0-9-]{1,30})\.?){1,}$/;
  return (
    <div className="center">
      <h1>Set up a new identity</h1>
      <h3 className={classes.h3}>Your Handshake name</h3>
      <form className={classes.form} onSubmit={(e) => { e.preventDefault(); props.createIndentity(name); }}>
        <TextField
          error={regexDomain.test(name)}
          required
          className={classes.nameInput}
          color="primary"
          label="Enter a Handshake name that you own"
          variant="outlined"
          onChange={e => _handleTextFieldChange(e)} />
        <div className={classes.action}>
          <div className='caption'>
            <span>Handshake name not in Namebase?</span>
            <span>See our <a href="https://docs.namebase.io/handshake-login/using-handshake-login" target="_new">instructions for settings the record<br /> outside of Namebase</a></span>
          </div>
          <ColorButton
            variant="contained"
            disabled={!regexDomain.test(name)}
            color="secondary"
            type="submit"
          >
            <Box fontWeight="600" component="div">
              Continue
            </Box>

          </ColorButton>
        </div>
        <div className={classes.bottom}>
          <div className='caption'>
            {props.identities.length === 0 ? (
              <Fragment>
                <span>Don’t have a Handshake name? </span>
                <span><a href="https://6cqg3vy1fsb.typeform.com/to/famUtbd0" target="_new">Request one for free</a></span>
              </Fragment>
            ): ''}
          </div>
        </div>
      </form>
    </div >
  );
}

export default AddIdentity;
