import React, { Fragment } from "react";
import { Divider, IconButton, List, ListItem, ListItemText, SvgIcon } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';

function ListIdentities(props: any) {

  const useStyles = makeStyles((theme) => ({
    root: {
      flexGrow: 1,
    },
    h3: {
      marginTop: theme.spacing(0),
      fontWeight: 400,
      fontSize: '14px',
      lineHeight: '24px',
      '& a': {
        color: '#0A6CFF'
      }
    },
    list: {
      width: '400px'
    },
    listItemText: {
      fontWeight: 500,
      fontSize: '14px',
      lineHeight: '24px',
      color: 'white',
      '&:hover': {
        color: '#0A6CFF' 
      }
    },
    divider: { backgroundColor: '#242424' }
  }));

  const classes = useStyles();

  function DeleteIcon(props: any) {
    return (
      <SvgIcon {...props}>
        <path d="M10.4062 15.1875H10.9688C11.1094 15.1875 11.25 15.082 11.25 14.9062V5.34375C11.25 5.20312 11.1094 5.0625 10.9688 5.0625H10.4062C10.2305 5.0625 10.125 5.20312 10.125 5.34375V14.9062C10.125 15.082 10.2305 15.1875 10.4062 15.1875ZM4.78125 15.1875H5.34375C5.48438 15.1875 5.625 15.082 5.625 14.9062V5.34375C5.625 5.20312 5.48438 5.0625 5.34375 5.0625H4.78125C4.60547 5.0625 4.5 5.20312 4.5 5.34375V14.9062C4.5 15.082 4.60547 15.1875 4.78125 15.1875ZM15.4688 2.25H11.8125L10.6172 0.703125C10.3359 0.316406 9.73828 0 9.28125 0H6.46875C5.97656 0 5.37891 0.316406 5.09766 0.703125L3.9375 2.25H0.28125C0.105469 2.25 0 2.39062 0 2.53125V3.09375C0 3.26953 0.105469 3.375 0.28125 3.375H1.125V16.3125C1.125 17.2617 1.86328 18 2.8125 18H12.9375C13.8516 18 14.625 17.2617 14.625 16.3125V3.375H15.4688C15.6094 3.375 15.75 3.26953 15.75 3.09375V2.53125C15.75 2.39062 15.6094 2.25 15.4688 2.25ZM6.01172 1.37109C6.08203 1.23047 6.29297 1.16016 6.46875 1.125H9.28125C9.42188 1.16016 9.63281 1.23047 9.70312 1.37109L10.4062 2.25H5.34375L6.01172 1.37109ZM13.5 16.3125C13.5 16.6289 13.2188 16.875 12.9375 16.875H2.8125C2.49609 16.875 2.25 16.6289 2.25 16.3125V3.375H13.5V16.3125ZM7.59375 15.1875H8.15625C8.29688 15.1875 8.4375 15.082 8.4375 14.9062V5.34375C8.4375 5.20312 8.29688 5.0625 8.15625 5.0625H7.59375C7.41797 5.0625 7.3125 5.20312 7.3125 5.34375V14.9062C7.3125 15.082 7.41797 15.1875 7.59375 15.1875Z" fill="#676767" />
      </SvgIcon>
    );
  }

  const params = new URLSearchParams(window.location.search);

  function getReferrerHeader() {
    if (!props.referrer) {
      return (<h3>&nbsp;</h3>);
    } else {
      let url = new URL(atob(params.get('referrer') || ''));
      url.pathname = atob(params.get('action')?.toString() || '');
      return (<h3 className={classes.h3}>to log in to <a href={url.protocol + '//' + url.hostname}>{url.hostname}</a></h3>)
    }
  }

  return (
    <div className="center">
      <h1>Choose an identity</h1>
      { getReferrerHeader()}
      <List component="nav" className={classes.list} aria-label="main mailbox folders">
        {
          props.identities.map((i: any) => {
            return (
              <Fragment key={i.name}><ListItem button >
                <ListItemText classes={{ primary: classes.listItemText }}
                  primary={i.name + '/'}
                  secondary={<React.Fragment>
                  </React.Fragment>}
                  onClick={async () => { props.referrer ? props.sendIdentity(i.name, atob(params.get('state')?.toString() || '')) : navigator.clipboard.writeText(i.fingerprint); }} />
                <IconButton edge="end" onClick={async () => { props.deleteIdentity(i.name) }} >
                  <DeleteIcon style={{ color: '#676767' }} />
                </IconButton>
              </ListItem>
                <Divider classes={{ root: classes.divider }} />
              </Fragment>
            )
          })
        }
        <Fragment>
          <ListItem button >
            <ListItemText classes={{ primary: classes.listItemText }}
              primary="+&nbsp;&nbsp;Set up another identity"
              secondary={<React.Fragment>
              </React.Fragment>}
              onClick={() => props.setCreation(true)} />
          </ListItem>
        </Fragment>
      </List>
    </div>
  );
}

export default ListIdentities;
